import { Component, OnInit, ViewChild } from '@angular/core';
import { BsDropdownDirective } from 'ngx-bootstrap';

// tslint:disable-next-line: class-name
export interface navItem {
  name: string;
  icon: string;
  path: string;
}

@Component({
  selector: 'app-dashboard-layout',
  templateUrl: './dashboard-layout.component.html'
})
export class DashboardLayoutComponent implements OnInit {
  @ViewChild('dropdown', { static: true })
  public dropdown: BsDropdownDirective;

  public isEng = true;
  public isNor = false;
  public isOpenSub = false;
  constructor() { }
  public navItems: navItem[] = [
    {
      name: 'Transactions',
      icon: 'icon-book-open',
      path: '/debtor/dashboard/transactions'
    },
    {
      name: 'Account',
      icon: 'icon-people',
      path: '/merchant/dashboard/account'
    },
    {
      name: 'Webshop',
      icon: 'icon-cart',
      path: '/merchant/dashboard/webshop'
    },
    {
      name: 'Documents',
      icon: 'icon-drawer',
      path: '/merchant/dashboard/documents'
    },
    {
      name: 'Card',
      icon: 'icon-credit-card',
      path: '/debtor/dashboard/card'
    },
    {
      name: 'Profile',
      icon: 'icon-people',
      path: '/debtor/dashboard/profile'
    },
    {
      name: 'Debtors',
      icon: 'icon-cart',
      path: '/admin/dashboard/debtors'
    },
    {
      name: 'Merchants',
      icon: 'icon-briefcase',
      path: '/admin/dashboard/merchants'
    },
    {
      name: 'Users',
      icon: 'icon-people',
      path: '/admin/dashboard/users'
    },
    {
      name: 'Card Payments',
      icon: 'icon-credit-card',
      path: '/admin/dashboard/card-payments'
    },
    {
      name: 'Payouts',
      icon: 'icon-wallet',
      path: '/admin/dashboard/payouts'
    },
    {
      name: 'System settings',
      icon: 'icon-settings',
      path: '/admin/dashboard/system-settings'
    }
  ];

  public sidebarMinimizeActive = false;
  public menuSmallDeviceActive = false;
  public isShowBackDrop = false;

  sidebarMinimize(): void{
    this.sidebarMinimizeActive = !this.sidebarMinimizeActive;
  }

  menuToggle(): void {
    this.menuSmallDeviceActive = !this.menuSmallDeviceActive;
    this.isShowBackDrop = !this.isShowBackDrop;
    const html = document.getElementsByTagName('html')[0];
    if (this.isShowBackDrop) {
      html.classList.add('show-backdrop');
    }
  }

  clickOutside(event): void {
    if (event) {
        this.isShowBackDrop = false;
    }
    const html = document.getElementsByTagName('html')[0];
    html.classList.remove('show-backdrop');
  }

  setDocHeight(): void {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
    // console.log('test vh = ' + vh);
  }


  ngOnInit() {
    this.setDocHeight();
    const html = document.getElementsByTagName('html')[0];
    const element =  document.getElementById('topAlertBar');
    if (typeof(element) !== 'undefined' && element != null) {
      html.classList.add('has-topbar');
      this.setTopBarHeight();
    }
  }

  setTopBarHeight(): void {
    const element =  document.getElementById('topAlertBar');
    const topBarHeight = element.offsetHeight;
    const mainWrapper = document.getElementsByClassName('main-wrapper')[0];
    if (typeof(element) !== 'undefined' && element != null) {
      mainWrapper.setAttribute('style' , 'margin-top:' + topBarHeight + 'px');
    }
  }

  onResize(event) {
    event.target.innerWidth;
    this.setDocHeight();
    this.setTopBarHeight();
  }

  selectEng(): void {
    this.isEng = true;
    this.isNor = false;
    this.dropdown.hide();
  }
  selectNor(): void {
    this.isEng = false;
    this.isNor = true;
    this.dropdown.hide();
  }

  toggleSubMenu(): void {
    this.isOpenSub = !this.isOpenSub;
  }

  OpenMinimize(): void {
    this.sidebarMinimizeActive = false;
  }
}
