import { Directive, ElementRef, Output, HostListener, EventEmitter } from '@angular/core';

@Directive({
  selector: '[appSidebar]'
})
export class SidebarDirective {

  constructor(private elementRef: ElementRef) { }

  @Output('onClickOutside') onClickOutside: EventEmitter<boolean> = new EventEmitter();

  @HostListener('document:click', ['$event.target']) onMouseEnter(targetElement: Element) {
    const isClickBackdrop = targetElement.classList.contains('backdrop-sidebar-open')
    || targetElement.classList.contains('sidebar-nav-link')
    || targetElement.classList.contains('sidebar-nav-icon')
    || targetElement.classList.contains('sidebar-icon-wrapper')
    || targetElement.classList.contains('menu-name')
    || targetElement.classList.contains('sidebar-mobile-minimize')
    || targetElement.classList.contains('icon-arrow-left');

    if (isClickBackdrop) {
      this.onClickOutside.emit(true);
    }
  }

}
