import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-default-layout',
  templateUrl: './default-layout.component.html'
})
export class DefaultLayoutComponent implements OnInit {
  public title: string;

  constructor() {

  }
  ngOnInit() {
    const html = document.getElementsByTagName('html')[0];
    const element =  document.getElementById('topAlertBar');
    // const onboardTopbar = document.getElementsByClassName('onboard-topbar')[0];
    if (typeof(element) !== 'undefined' && element != null) {
      html.classList.add('has-topbar');
      this.setTopBarHeight();
    }
  }

  setTopBarHeight(): void {
    const element =  document.getElementById('topAlertBar');
    const topBarHeight = element.offsetHeight;
    const mainWrapper = document.getElementsByClassName('main')[0];
    const onboardTopbar = document.getElementsByClassName('onboard-topbar')[0];
    if (typeof(element) !== 'undefined' && element != null) {
      mainWrapper.setAttribute('style' , 'padding-top:' + (topBarHeight * 2 + 20) + 'px');
      onboardTopbar.setAttribute('style' , 'top:' + topBarHeight + 'px');
    }
  }

  onResize(event) {
    event.target.innerWidth;
    this.setTopBarHeight();
  }

}
